import uniStatusBar from '../uni-status-bar/uni-status-bar.vue'
import uniIcon from '../uni-icon/uni-icon.vue'

export default {
	name: 'UniNavBar',
	components: {
		uniStatusBar,
		uniIcon
	},
	props: {
		title: {
			type: String,
			default: '世纪祭祀'
		},
		leftText: {
			type: String,
			default: ''
		},
		rightText: {
			type: String,
			default: ''
		},
		leftIcon: {
			type: String,
			default: ''
		},
		rightIcon: {
			type: String,
			default: ''
		},
		fixed: {
			type: Boolean,
			default: true
		},
		color: {
			type: String,
			default: '#fff'
		},
		backgroundColor: {
			type: String,
			default: '#789eeb'
		},
		statusBar: {
			type: Boolean,
			default: true
		},
		shadow: {
			type: Boolean,
			default: false
		},
		border: {
			type: Boolean,
			default: false
		}
	},
	methods: {
		onClickLeft() {
			if(this.leftIcon.length>0&&this.leftIcon=='back'){
				uni.navigateBack({
					delta: 1
				})
			}else{
				this.$emit('click-left')
			}
			
		},
		onClickRight() {
			this.$emit('click-right')
		}
	}
}
